import {getFormattedDate, getDuration, formateDuration} from '../utils/dates.js';
import he from 'he';
import AbstractView from '../framework/view/abstract-view.js';


function createTemplate(point, offers, destination) {
  const {dateFrom, dateTo, isFavorite} = point;
  const pointType = he.encode(point.type);
  const pointPrice = he.encode(String(point.basePrice));

  const dateStart = getFormattedDate(dateFrom, 'MMM DD');
  const datetimeStart = getFormattedDate(dateFrom, 'YYYY-MM-DD');
  const timeStart = getFormattedDate(dateFrom, 'HH:mm');

  const datetimeEnd = getFormattedDate(dateTo, 'YYYY-MM-DD');
  const timeEnd = getFormattedDate(dateTo, 'HH:mm');

  const duration = formateDuration(getDuration(dateFrom, dateTo));
  const favoriteClass = (isFavorite) ? 'event__favorite-btn--active' : '';

  const offersTemplate = offers.reduce((template, offer) => `${template}
      <li class="event__offer">
        <span class="event__offer-title">
          ${he.encode(offer.title)}
        </span>
        +&euro;&nbsp;
        <span class="event__offer-price">
          ${he.encode(String(offer.price))}
        </span>
      </li>
    `, '');

  return /*html*/`
    <li class="trip-events__item">
      <div class="event">
        <time class="event__date"
          datetime="${datetimeStart}">
          ${dateStart}
        </time>
        <div class="event__type">
          <img class="event__type-icon" width="42" height="42" alt="Event type icon"
            src="img/icons/${pointType.toLowerCase()}.png"
          >
        </div>
        <h3 class="event__title">
          ${pointType} ${he.encode(destination.name)}
        </h3>
        <div class="event__schedule">
          <p class="event__time">
            <time class="event__start-time"
              datetime="${datetimeStart}T${timeStart}">
                ${timeStart}
            </time>
            
            <time class="event__end-time"
              datetime="${datetimeEnd}T${timeEnd}">
                ${timeEnd}
            </time>
          </p>
          <p class="event__duration">
            ${duration}
          </p>
        </div>
        <p class="event__price">
          &euro;&nbsp;
          <span class="event__price-value">
            ${pointPrice}
          </span>
        </p>
        <h4 class="visually-hidden">Offers:</h4>
        <ul class="event__selected-offers">
          ${offersTemplate}
        </ul>
        <button class="event__favorite-btn ${favoriteClass}" type="button">
          <span class="visually-hidden">Add to favorite</span>
          <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
            <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"></path>
          </svg>
        </button>
        <button class="event__rollup-btn" type="button">
          <span class="visually-hidden">Open event</span>
        </button>
      </div>
    </li>
  `;
}

export default class PointView extends AbstractView {
  #point = {};
  #offers = [];
  #destination = {};
  #handleEditClick = null;
  #handleFavoriteClick = null;

  constructor ({point, offers, destination, onEditClick, onFavoriteClick}) {
    super();
    this.#point = point;
    this.#offers = offers;
    this.#destination = destination;
    this.#handleEditClick = onEditClick;
    this.#handleFavoriteClick = onFavoriteClick;

    this.element.querySelector('.event__rollup-btn').addEventListener('click', this.#editClickHandler);
    this.element.querySelector('.event__favorite-btn ').addEventListener('click', this.#favoriteClickHandler);
  }

  get template() {
    return createTemplate(this.#point, this.#offers, this.#destination);
  }

  #editClickHandler = (evt) => {
    evt.preventDefault();
    this.#handleEditClick();
  };

  #favoriteClickHandler = (evt) => {
    evt.preventDefault();
    this.#handleFavoriteClick();
  };
}
